import { AxiosInstance, default as cleanAxios } from 'axios'
import {
  AtomicTx,
  CallContractTx,
  ContractParam,
  CreateContractTx,
  TransactionBroadcaster,
} from '@wavesenterprise/voting-blockchain-tools/transactions'
import { WaitTransactionMining } from '../../utils/waitUtils'
import { formatContractParams } from '../../utils/convertUtils'

import { DEFAULTS } from '../../index'
import { IDockerCallParams, DockerCallTxParams, DockerCallTxParamsWithAtomic } from '../../types/general'
import { IModifiedWavesApiResponse, IWavesApiResponse } from '../../types/node'
import { CreateContractParams, IVotingContractAPICtr } from '../../types/contractApi'
import { TxInfoFetcher } from '../../utils/txInfoUtils'

export abstract class VotingContract {
  private readonly config: IVotingContractAPICtr
  protected readonly transactionBroadcaster: TransactionBroadcaster
  protected readonly axiosInstance: AxiosInstance
  protected readonly nodeAddress: string
  private readonly txInfoFetcher: TxInfoFetcher

  protected constructor(cfg: IVotingContractAPICtr) {
    this.config = {
      ...DEFAULTS,
      ...cfg,
    }
    const {
      transactionBroadcaster,
      axiosInstance,
      nodeAddress,
    } = this.config

    this.nodeAddress = nodeAddress.replace(/\/+$/, '')
    this.axiosInstance = axiosInstance ?? cleanAxios
    this.transactionBroadcaster = transactionBroadcaster
    this.txInfoFetcher = new TxInfoFetcher(
      this.nodeAddress,
      this.axiosInstance,
    )
  }

  protected createDockerContract = async (
    params: CreateContractParams,
  ): Promise<IModifiedWavesApiResponse> => {
    const {
      txParams: { fee, contractName, image, imageHash, feeAssetId, skipWaitTransactionMining, validationPolicy },
      contractParams,
    } = params

    const transaction = new CreateContractTx({
      image,
      imageHash,
      contractName,
      fee,
      validationPolicy: validationPolicy ?? { type: 'any' },
      feeAssetId: feeAssetId ?? this.config.feeAssetId ?? undefined,
      senderPublicKey: this.config.keyPair.publicKey,
      params: formatContractParams(contractParams),
      timestamp: Date.now(),
      apiVersion: '1.0',
    })

    const result: IWavesApiResponse = await this.transactionBroadcaster.broadcast(transaction, this.config.keyPair)

    if (this.config.isWaitTransactionMining && !skipWaitTransactionMining) {
      return this.waitMining(result)
    }

    return {
      data: result,
      success: true,
    }
  }

  protected createOperationParam = (operationName: string): ContractParam => {
    return {
      type: 'string',
      key: 'operation',
      value: operationName,
    }
  }

  protected async callDockerContract <T extends Record<string, unknown>>(
    operationParam: ContractParam,
    params: IDockerCallParams<T, DockerCallTxParams | DockerCallTxParamsWithAtomic>,
  ): Promise<IModifiedWavesApiResponse> {
    const { txParams: { skipWaitTransactionMining, useAtomic } } = params

    const transaction = this.createCallContractTx(operationParam, params, { useAtomic })

    let result: IWavesApiResponse

    if (useAtomic) {
      const signedContractCall = await this.transactionBroadcaster.getSignedTx(
        transaction,
        this.config.keyPair,
        { addTxId: true },
      )
      const { txParams: { transactions } } = params as IDockerCallParams<T, DockerCallTxParamsWithAtomic>
      const atomic = new AtomicTx({
        senderPublicKey: this.config.keyPair.publicKey,
        transactions: [...transactions, signedContractCall],
      })
      const broadcastRes = await this.transactionBroadcaster.broadcast(atomic, this.config.keyPair)
      result = broadcastRes.transactions[broadcastRes.transactions.length - 1]
    } else {
      result = await this.transactionBroadcaster.broadcast(transaction, this.config.keyPair)
    }

    if (this.config.isWaitTransactionMining && !skipWaitTransactionMining) {
      return this.waitMining(result)
    }

    return {
      data: result,
      success: true,
    }
  }

  private createCallContractTx = <T extends Record<string, unknown>>(
    operationParam: ContractParam,
    params: IDockerCallParams<T, DockerCallTxParams>,
    options: { useAtomic?: boolean } = { useAtomic: false },
  ) => {
    const {
      txParams: { contractId, fee, timestamp, feeAssetId },
      contractParams,
    } = params

    return new CallContractTx({
      contractId,
      fee,
      senderPublicKey: this.config.keyPair.publicKey,
      feeAssetId: feeAssetId ?? this.config.feeAssetId ?? undefined,
      params: [operationParam, ...formatContractParams(contractParams)],
      timestamp: timestamp ?? Date.now(),
      contractVersion: 1,
      ...(options.useAtomic
        ? { atomicBadge: { trustedSender: this.config.keyPair.address } }
        : {}
      ),
    })
  }

  private async waitMining(result: IWavesApiResponse) {
    const waitTransactionMining = new WaitTransactionMining(
      this.nodeAddress,
      this.axiosInstance,
      this.config.statusResponseTimeout!,
      this.config.maxMiningAttempts,
      result.id,
    )

    const status = await waitTransactionMining.waitMining()
    if (status.success && this.config.extendedTransactionInfo) {
      const txInfo = await this.txInfoFetcher.getTransactionInfo(result.id)
      return {
        data: {
          ...result,
          height: txInfo.height,
        },
        ...status,
      }
    }
    return {
      data: result,
      ...status,
    }
  }
}
