import { IStatusResponse, NodeMinerResponse, WaitMiningResult } from '../types/node'
import { AxiosError, AxiosInstance } from 'axios'

class WaitMilliseconds {
  private timeout: NodeJS.Timeout | null = null
  private promiseResolve: ((isReached: boolean) => void) | null = null

  wait(ms: number): Promise<boolean> {
    return new Promise((resolve) => {
      this.promiseResolve = resolve
      this.timeout = setTimeout(() => {
        resolve(true)
      }, ms)
    })
  }

  cancel() {
    if (this.timeout) {
      clearTimeout(this.timeout)
    }
    if (this.promiseResolve) {
      this.promiseResolve(false)
    }
  }
}

export class WaitTransactionMining {
  private waitTimeout = new WaitMilliseconds()
  private waitRequest = new WaitMilliseconds()

  constructor(
    private readonly apiUrl: string,
    private readonly axiosInstance: AxiosInstance,
    private readonly responseTimeout: number,
    private readonly maxMineErrors: number | null = null,
    private readonly txId: string,
    private readonly requestWaitTimeout: number = 2000,
  ) {
  }

  async waitMining(): Promise<WaitMiningResult> {
    let isFinished = false
    let lastResponse: IStatusResponse | undefined

    const requestStatus = async (wait: number): Promise<WaitMiningResult> => {
      const isReached = await this.waitRequest.wait(wait)
      if (!isReached) {
        return {
          isCanceled: true,
          ...lastResponse || this.customErrorResponse(this.txId, ''),
          miningDescription: 'Timeout',
        }
      }
      const res = await this.getTransactionStatus(this.txId)
      lastResponse = res
      if (isFinished) {
        return {
          ...lastResponse,
          miningDescription: 'Timeout',
        }
      }

      if (lastResponse.success) {
        return {
          ...lastResponse,
          miningDescription: 'Success',
        }
      }


      if (Array.isArray(lastResponse.status) && lastResponse.status.some(({ status }) => status === 'Error')) {
        return {
          ...lastResponse,
          miningDescription: 'Error mining status',
        }
      }

      if (this.maxMineErrors !== null) {
        const errorsNumber = Array.isArray(lastResponse.status) && lastResponse.status.reduce((n, response) => response.status === 'Success' ? n : n + 1, 0)
        if (errorsNumber && errorsNumber >= this.maxMineErrors) {
          return {
            ...lastResponse,
            miningDescription: 'Max mining attempts',
          }
        }
      }

      return requestStatus(this.requestWaitTimeout)
    }

    const result = await Promise.race([
      requestStatus(0),
      this.waitTimeout.wait(this.responseTimeout).then((isReached) => {
        if (isReached) {
          return {
            ...(lastResponse || this.customErrorResponse(this.txId, 'wait status timeout')),
            miningDescription: 'Timeout',
          } as WaitMiningResult
        }
        return {
          isCancelled: true,
          ...lastResponse || this.customErrorResponse(this.txId, ''),
          miningDescription: 'Timeout',
        } as WaitMiningResult
      }),
    ])

    this.waitRequest.cancel()
    this.waitTimeout.cancel()
    isFinished = true

    return result
  }

  cancelWaiting() {
    this.waitRequest.cancel()
    this.waitTimeout.cancel()
  }

  private getTransactionStatus = async (txId: string): Promise<IStatusResponse> => {
    try {
      const { data } = await this.axiosInstance.get<NodeMinerResponse[]>(
        `${this.apiUrl}/contracts/status/${txId}`,
      )

      const success = Array.isArray(data) && data.some(
        nodeResponse => nodeResponse.status === 'Success',
      )

      return {
        success,
        status: data.map((row) => {
          const { status, message } = row
          const isValidator = status === 'Error' && message.includes('Validator error: ')
          return {
            ...row,
            isValidator,
            message: isValidator ? message.replace(/Validator error: /, '') : message,
          }
        }),
      }
    } catch (err) {
      if ((err as AxiosError)?.response?.status === 404) {
        return {
          success: false,
          status: [],
        }
      }
      return this.customErrorResponse(txId, this.getAxiosError(err))
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private getAxiosError(err: any) {
    return err?.response?.data
      ? JSON.stringify(err.response.data, null, 2)
      : err.message as string
  }

  private customErrorResponse(txId: string, error: string) {
    return {
      success: false,
      status: [{
        sender: '',
        senderPublicKey: '',
        txId,
        timestamp: Date.now(),
        status: 'Error' as 'Error',
        message: error,
        code: 500,
        signature: '',
        isValidator: false,
      }],
    }
  }
}
