import { VotingContract } from './votingContract'

import { IModifiedWavesApiResponse } from '../../types/node'
import {
  FinishRoundParams,
  IAddMainKey,
  IDecryptionParams,
  IDkgCommitParams,
  IDkgComplaintParams,
  IDkgScalarParams,
  IDkgShadowsParams,
  IResultsParams,
  IUpdateServerListParams,
  IVotingContractAPICtr,
  StartRoundParams,
  WeAddParticipantsParams,
  WeCreateContractParams,
  WeInitiateVotingParams,
  WeVoteParams,
} from '../../types/contractApi'
import { DockerCallTxParams, DockerCallTxParamsWithAtomic, IDockerCallParams } from '../../types/general'

export class WeVotingContract extends VotingContract {

  constructor(ctr: IVotingContractAPICtr) {
    super(ctr)
  }

  createContract = (
    params: WeCreateContractParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.createDockerContract(params)
  }

  initiateVoting = (
    params: WeInitiateVotingParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('initiateVoting'),
      params,
    )
  }

  vote(
    params: IDockerCallParams<WeVoteParams, DockerCallTxParamsWithAtomic | DockerCallTxParams>,
  ): Promise<IModifiedWavesApiResponse> {
    return this.callDockerContract(
      this.createOperationParam('vote'),
      params,
    )
  }

  addParticipants = (
    params: WeAddParticipantsParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('addParticipants'),
      params,
    )
  }

  updateServerList = (
    params: IUpdateServerListParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('updateServerList'),
      params,
    )
  }

  addMainKey = (
    params: IAddMainKey,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('addMainKey'),
      params,
    )
  }

  results = (
    params: IResultsParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('results'),
      params,
    )
  }

  dkgCommit = (
    params: IDkgCommitParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('dkgCommit'),
      params,
    )
  }

  dkgScalar = (
    params: IDkgScalarParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('dkgScalar'),
      params,
    )
  }

  dkgShadows = (
    params: IDkgShadowsParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('dkgShadows'),
      params,
    )
  }

  dkgComplaint = (
    params: IDkgComplaintParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('dkgComplaint'),
      params,
    )
  }

  decryption = (
    params: IDecryptionParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('decryption'),
      params,
    )
  }

  finishVoting = (
    // eslint-disable-next-line @typescript-eslint/ban-types
    params: IDockerCallParams<{}>,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('finishVoting'),
      params,
    )
  }

  startVoting = (
    // eslint-disable-next-line @typescript-eslint/ban-types
    params: IDockerCallParams<{}>,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('startVoting'),
      params,
    )
  }

  startRound = (
    params: StartRoundParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('startRound'),
      params,
    )
  }

  finishRound = (
    params: FinishRoundParams,
  ): Promise<IModifiedWavesApiResponse> => {
    return this.callDockerContract(
      this.createOperationParam('finishRound'),
      params,
    )
  }
}
