import { ExtendedWavesApiResponse } from '../types/node'
import { AxiosInstance } from 'axios'

export class TxInfoFetcher {

  constructor(
    private readonly apiUrl: string,
    private readonly axiosInstance: AxiosInstance,
  ) {
  }

  getTransactionInfo = async (txId: string): Promise<ExtendedWavesApiResponse> => {
    const { data } = await this.axiosInstance.get(`${this.apiUrl}/transactions/info/${txId}`)
    return data as ExtendedWavesApiResponse
  }

}
