import { ContractParam } from '@wavesenterprise/voting-blockchain-tools/transactions'
import { getDateString, isDateInstance } from './dateUtils'

class MapContractParamsError extends Error {
  constructor(key: string) {
    super(`Error. Cannot map contract params. Unknown type for key: ${key}.`)
    this.name = 'MapContractParamsError'
  }
}

export const formatContractParams = (object: Record<string, unknown>): ContractParam[] => {
  return Object.entries(object)
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    .filter(([_1, value]) => value !== undefined)
    .map(([key, value]) => {
      switch (typeof value) {
        case 'string':
          return {
            type: 'string',
            key,
            value,
          }
        case 'number':
          return {
            type: 'integer',
            key,
            value,
          }
        case 'boolean':
          return {
            type: 'boolean',
            key,
            value: !!value,
          }
        case 'object':
          if (isDateInstance(value)) {
            return {
              type: 'string',
              key,
              value: getDateString(value),
            }
          }
          return {
            type: 'string',
            key,
            value: JSON.stringify(value),
          }
        default:
          throw new MapContractParamsError(key)
      }
    })
}
